  min-width: 200px;
  background:#353333;
  color:white;
  text-align: center;
  margin:0;
  padding-top:20px;
}


/* Top Section */
.head{
    padding:10px;
    min-width: 200px;

    font-size:20px;
    
    background-color:rgb(53, 51, 51);
    color:white;

    display: -webkit-flex;

    display: flex; 
    -webkit-justify-content:center; 
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
    
}
