    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
}
.level{
    width:40px;
    height:40px;
    padding:7px 12px;
    margin:0;
    border-radius:50px;

    background:#353333;
    color:rgb(253, 252, 252);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
}



/* question and answer */
.queAnsSection{
    /* height:496px; */
    min-width: 200px;
    background:#7FB7BE;
    padding:10px 30px 50px 30px;

    position:relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.category{
    margin: 20px 10px 20px 10px;

    font-size: 18px;
    text-align: center;
    color:white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
/* question */
.question{
    min-height:33%;
    margin:10px;
    padding:30px;

    text-align: justify;

    background:white;
    border-radius:20px;
    box-shadow:0 2px 5px black;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

}

/* answerResult */

.answerResult{
    width:75%;
    /* height:400px; */
    padding:0px;
    padding-bottom: 20px;

    background:rgb(37, 37, 37);
    color:rgb(253, 250, 250);
    border:10px solid black;
    border-radius:20px;
    box-shadow:0 2px 5px black;

    font-size: 20px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;

    position: absolute;
    top:15px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    z-index: 3;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.answerResult p:nth-child(3){
    margin:0px;
}
.answerResult p:nth-child(1){
    width:100%;
    height:50px;

    font-size: 25px;

    background:rgb(235, 184, 16);
    color:rgb(0, 0, 0);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
}
.answerResult p:nth-child(4){
    width:100px;
    border-radius:30px;

    font-size: 30px;
    color:white;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
}

.answerResult button{
    width:100px;
    height:35px;
    margin:5px;

    font-size: 18px;
    border-radius:20px;

    color:rgb(0, 0, 0);
    background:rgb(231, 199, 18);

    border:none;
    cursor: pointer;
    outline:none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.answerResult button:hover{
    background: rgb(91, 202, 27);
}


/* answers */
.answers{
    width:100%;
    min-width:200px ;
    height:50%;
    margin: 10px;
    padding:20px;

    background:transparent;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items:center;
            align-items:center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    
}
.items{
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width:100%;
    min-width: 200px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.items button{
    /* min-width: 150px; */
    width:45%;
    
    min-height:60px;
    margin:10px 15px;
    padding:20px;
    border-radius:20px;
    box-shadow:0 2px 5px black;

    text-align:center;
    background:white;

    -webkit-transition: all 0.2s;

    transition: all 0.2s;
    outline:none;
    border:none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.items button:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: rgb(73, 76, 80);
    color:white;
}


